
import {defineComponent} from 'vue';
// тип нужных параметров
export type Login = {
  email: string,
  password: string
}
export default defineComponent({
  name: 'AuthLogin',
  data: () => ({
    // валидно или нет
    valid: true,
    // скрытие пароля
    show1: false,
    // пароль
    password: '',
    // валидация пароля
    passwordRules: {
      required: (value: string) => !!value || 'Пароль невалиден',
      min: (v: string) => v.length >= 8 || 'Минимум 8 символов',
    },
    // почта
    email: '',
    // валидация почты
    emailRules: [
      (v: string) => !!v || 'Почта не валидна',
      (v: string) => /.+@.+\..+/.test(v) || 'Почта не валидна',
    ]
  }),

  methods: {
    // Вход в систему
    async validate() {
      const {valid} = await (this.$refs['form'] as any).validate()
      if (!valid) alert('Форма не валидна')
      const payload: Login = {
        email: this.email,
        password: this.password
      }
      try {
        await this.$store.dispatch('auth/login', payload)
        await (this.$refs['form'] as any).reset()
        this.$router.push("/")
      } catch (err) {
        console.log(err)
      }

    }
  }
})
